import { getComments } from "@/pages/api/article.js";

export default {
  name: "ArticleComments",
  components: {},
  props: {
    article: {
      type: Object,
      require: true,
    },
  },
  data() {
    return {
      comments: [],
    };
  },
  computed: {},
  watch: {},
  methods: {},
  created() {},
  async mounted() {
    const { data } = await getComments(this.article.slug);
    console.log("sujie", data);
    this.comments = data.comments;
  },
};
